section.workflow-section {
    background-color: var(--dark);
    background: url('../assets/back-dark-1.png');
    background-position: center;
    background-size: cover;
    /* height: 1400px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    padding: 2rem 1rem;
    padding-top: 4rem;
}

section.workflow-section>h1 {
    color: var(--white);
    font-size: 5rem;
    margin-bottom: 3rem;
    font-weight: bolder;
}

@media screen and (max-width: 800px) {
    section.workflow-section>h1 {
        font-size: 2.5rem;
    }
    section.workflow-section {
        padding: 2rem 1rem;
        padding-top: 3rem;
    }
}

.workflow-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100%;
}

span.workflow-line {
    width: 2px;
    height: 100%;
    background: rgb(0, 0, 0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(99, 7, 242, 1) 25%, rgba(99, 7, 242, 1) 75%, rgba(0, 0, 0, 0) 100%);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.workflow-card {
    position: relative;
    overflow: hidden;
    padding: 2rem 2rem;
    padding-top: 4rem;
    border-radius: 1rem;
    border: 2px solid var(--pri);
    background-color: var(--dark);
    width: 50%;
    margin: 1rem 0rem;
}

.workflow-card h1.number {
    position: absolute;
    font-size: 12rem;
    color: var(--pri);
    opacity: .4;
    top: 30%;
    left: -2%;
    font-weight: bolder;
    z-index: 0;
    line-height: 0px;
}

.workflow-card h1.title {
    position: relative;
    margin-bottom: 1rem;
    font-weight: bolder;
}

.workflow-card p {
    font-size: 1.3rem;
}

.workflow-card h1.title::before {
    position: absolute;
    content: '';
    width: 10%;
    height: 5px;
    background-color: var(--white);
    top: 90%;
    left: 1%;
}

.workflow-card h1,
.workflow-card p {
    position: relative;
    color: var(--white);
    margin: 0px;
    z-index: 10;
}

@media screen and (max-width: 800px) {
    .workflow-card {
        width: 80%;
    }
}

@media screen and (max-width: 600px) {
    .workflow-card {
        width: 100%;
    }
}