section.works-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url('../assets/back-dark-1.png');
    background-position: center;
    background-size: cover;
    padding: 3rem 2rem;
}

section.works-section>h1 {
    font-size: 3.5rem;
    font-weight: 600;
    line-height: 3rem;
    color: var(--white);
    text-align: center;
}

section.works-section>h1>span {
    color: var(--pri);
}

section.works-section .works-container {
    max-width: 1200px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 3rem 0rem;
}

.works-card {
    padding: .5rem;
    transition: 200ms;
}

.works-card:hover {
    background-color: rgba(0, 0, 0, 0.3);
}

.works-card .work-thumbnail {
    overflow: hidden;
}

.works-card .work-thumbnail>img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    transition: 300ms;
}

.works-card:hover .work-thumbnail>img {
    scale: 1.1;
}

.work-info-container {
    padding: 1rem 0rem;
}

.works-card a {
    text-decoration: none;
}

.works-card h3 {
    position: relative;
    color: var(--white);
    display: flex;
    justify-content: start;
    align-items: center;
    text-decoration: none;
}

.works-card h3::after {
    position: absolute;
    content: '';
    height: 2px;
    width: 0%;
    top: 90%;
    background-color: var(--white);
    transition: 300ms;
}

.works-card:hover h3::after {
    width: 90%;
}

.works-card h3 span {
    opacity: 0;
    transform: translateX(-10px);
    font-size: 1.5rem;
    margin-left: .5rem;
    transition: 300ms;
}

.works-card:hover h3 span {
    opacity: 1;
    transform: translateX(0px);
}

.works-card h5 {
    color: var(--white);
}

@media screen and (max-width:1200px) {
    section.works-section .works-container {
        grid-template-columns: repeat(2, 1fr);
        padding: 3rem 0rem;
    }
}

@media screen and (max-width:650px) {
    section.works-section {
        padding: 1rem 1rem;
    }

    section.works-section .works-container {
        grid-template-columns: repeat(1, 1fr);
        padding: 1rem 0rem;
    }

    section.works-section>h1 {
        font-size: 2.5rem;
    }
}