section.contact-section {
    background-color: var(--dark);
    background: url('../assets//back-dark-1.png');
    background-position: center;
    background-size: cover;
    padding: 3rem 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact-container {
    max-width: 700px;
    padding: 1rem 2rem;
    border-radius: 1rem;
    background-color: rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(50px);
}

.contact-container h1 {
    color: var(--white);
    font-size: 3rem;
    text-align: center;
}

.contact-container h1 span {
    color: var(--pri)
}

.contact-container p {
    color: var(--white);
    font-size: 1.5rem;
    text-align: center;
    opacity: .6;
}

form.contact-form {
    display: flex;
    flex-direction: column;
}

form.contact-form input,
form.contact-form select,
form.contact-form textarea,
form.contact-form button {
    border: 1px inset rgba(255, 255, 255, .2);
    outline: none;
    border-radius: .5rem;
    padding: .6rem .5rem;
    font-size: 1rem;
    margin: .5rem 0rem;
    color: rgba(255, 255, 255, .5);
    background-color: rgba(0, 0, 0, .2);
}

form.contact-form input:focus,
form.contact-form select:focus,
form.contact-form textarea:focus {
    border: 1px inset rgba(255, 255, 255, .5);
}

form.contact-form input::placeholder,
form.contact-form select::placeholder,
form.contact-form textarea::placeholder {
    color: rgba(255, 255, 255, .5);
}

form.contact-form option {
    background-color: var(--sec);
    color: white;
}

form.contact-form textarea {
    min-height: 150px;
    max-height: 300px;
}

form.contact-form button {
    background-color: var(--pri);
    color: var(--white);
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px;
}

@keyframes moveDots {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

form.contact-form button span#dots {
    white-space: nowrap;
    animation: moveDots 2s infinite;
    overflow: hidden;
}

@media screen and (max-width: 750px) {
    section.contact-section {
        padding: 2rem 1rem;
    }

    .contact-container {
        padding: 1rem 1rem;
    }

    .contact-container h1 {
        font-size: 2.5rem;
    }

    .contact-container p {
        font-size: 1rem;
    }
}