section.plans-section {
    background-color: var(--dark);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem 3rem;
}

section.plans-section>h1 {
    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
    color: var(--white);
    text-align: center;
}

section.plans-section>p {
    text-align: center;
    color: var(--white);
}

section.plans-section h1>span {
    color: var(--pri);
}

div.plans-container {
    padding: 2rem 0rem;
    max-width: 1000px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}

div.plan-card {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding: 2rem 2rem;
    border-radius: 1rem;
    box-shadow: 0px 4px 25px 0px rgba(0, 0, 0, 0.15);
    background-color: var(--sec);
}

div.plan-card h1.plan-title {
    font-size: 2.5rem;
    font-weight: bold;
    color: var(--white);
}

div.plan-card h1.plan-price {
    font-size: 3.5rem;
    font-weight: bolder;
    color: var(--white);
}

div.plan-card p {
    font-size: 1rem;
    line-height: 1.3rem;
    color: var(--white);
}

div.plan-card h3 {
    font-size: 1.5rem;
    color: var(--white);
    font-weight: bold;
}

@counter-style thumbs {
    system: cyclic;
    symbols: ✅;
    suffix: " ";
}

div.plan-card ul {
    color: var(--white);
    list-style-type: thumbs;
}

div.plan-card button {
    padding: 1rem;
    color: var(--white);
    background-color: var(--pri);
    border-radius: .5rem;
    width: 100%;
    transition: 200ms;
}

div.plan-card button:hover {
    color: var(--white);
    background-color: var(--dark);
}

@media screen and (max-width:1100px) {
    div.plans-container {
        padding: 3rem 4rem;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width:900px) {
    div.plans-container {
        padding: 3rem 2rem;
    }
}

@media screen and (max-width:800px) {
    div.plans-container {
        padding: 3rem 2rem;
        grid-template-columns: 1fr;

    }
}

@media screen and (max-width:600px) {
    section.plans-section {
        padding: 2rem 1rem;
    }

    div.plans-container {
        padding: 2rem 0rem;
    }
}

/* modal */
section.plan-modal-section {
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    min-height: 100vh;
    height: 100%;
    background: rgba(0, 0, 0, 0.647);
    backdrop-filter: blur(5px);
    z-index: 10000000000000;
    display: flex;
    justify-content: center;
    align-items: start;
    padding: 2rem 0rem;
}

.plan-modal {
    position: relative;
    width: 80%;
    height: auto;
    max-height: 80vh;
    background-color: var(--light);
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    padding: 2rem;
    overflow-y: auto;
}

.plan-modal .close-btn {
    position: absolute;
    top: 1rem;
    right: 1rem;
    color: var(--dark);
    font-size: 1.5rem;
    background-color: transparent;
}

.plan-modal a.contact {
    color: var(--white);
    background-color: var(--pri);
    padding: .5rem 1rem;
    border-radius: .3rem;
    text-decoration: none;
    transition: 200ms;
}

.plan-modal a.contact:hover {
    color: var(--white);
    background-color: var(--dark);
}

.plan-modal h1,
.plan-modal p,
.plan-modal li {
    color: var(--dark);
}