header.header {
    position: fixed;
    background-color: var(--sec);
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    display: grid;
    justify-content: space-between;
    grid-template-columns: 1fr 4fr 1fr;
    grid-template-rows: auto;
    align-items: center;
    grid-gap: 4px;
    z-index: 10000;
}



header.header .logo-container {
    justify-self: start;
}

header.header .logo-container img.logo {
    height: 2rem;
}

header.header nav.header-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: start;
    margin-left: 8px;
}

header.header nav.header-nav .navlinks>a {
    text-decoration: none;
    color: var(--white);
    padding: .5rem 1rem;
    transition: 150ms;
    white-space: nowrap;
}

header.header nav.header-nav .navlinks>a:hover {
    color: var(--pri);
}

header.header .action-btns-container {
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: end;
}

header.header nav.header-nav .btns-in-nav {
    display: none;
}


a.contact-btn {
    text-decoration: none;
    outline: none;
    border: none;
    background-color: var(--pri);
    color: var(--white);
    border-radius: .5rem;
    padding: .5rem 1rem;
    margin-right: 4px;
}

header.header button#menuBtn {
    display: none;
}

/* responsive */
@media screen and (max-width:1100px) {
    header.header {
        grid-template-columns: auto auto;
    }

    header.header nav.header-nav {
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: end;
        position: absolute;
        background-color: var(--sec);
        width: 75%;
        top: 100%;
        right: 0px;
        margin-left: 0px;
        padding: 2rem 1rem;
        height: auto;
        min-height: 100vh;
        z-index: 10000;
        max-width: 400px;
    }

    header.header nav.header-nav .navlinks {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: end;
    }

    header.header nav.header-nav .navlinks a {
        font-size: 1.2rem;
    }

    header.header .action-btns-container {
        display: none;
    }

    header.header nav.header-nav .btns-in-nav {
        display: flex;
        flex-direction: column;
        row-gap: 6px;
    }

    header.header nav.header-nav .btns-in-nav a.contact-btn {
        margin: 0px;
        margin-right: 1rem;
    }


    header.header button#menuBtn {
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
        outline: none;
        background-color: transparent;
        color: var(--pri);
        font-size: 1.2rem;
    }
}

@media screen and (max-width:600px) {
    header.header .logo-container img.logo {
        height: 1.5rem;
    }
}