section.skillset-section {
    padding-top: 1rem;
    background-color: var(--dark);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

section.skillset-section>h1 {
    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
    color: var(--white);
    text-align: center;
}

section.skillset-section>p {
    text-align: center;
    color: var(--white);
}

section.skillset-section h1>span {
    color: var(--pri);
}

.skillset-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1px;
    padding: 1px;
    background-color: var(--white);
}

.skillset-card {
    padding: 1rem;
    background-color: var(--pri);
    /* border: 1px solid var(--pri); */
    /* aspect-ratio: 1; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: .3rem;
    cursor: pointer;
    transition: 200ms;

    &:hover {
        background-color: var(--white);
    }
}

.skillset-card>h5,
.skillset-card>p {
    transition: 200ms;
    color: var(--white);
}

.skillset-card:hover>h5,
.skillset-card:hover>p {
    color: var(--pri);
}

.skillset-card img {
    width: 100%;
    max-width: 5rem;
}

@media screen and (max-width: 1100px) {
    .skillset-container {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (max-width: 600px) {
    .skillset-container {
        grid-template-columns: repeat(2, 1fr);
    }
}