section.hero-section {
    padding-top: 5rem;
    background-color: var(--dark);
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
}


section.hero-section img#back_el_2 {
    position: absolute;
    top: 0%;
    right: 0%;
    height: auto;
    opacity: .5;
}



.hero-left {
    position: relative;
    padding: 2rem;
    flex-basis: 1;
}

.hero-left h3 {
    color: var(--pri);
    font-weight: 800;
}

.hero-left h1 {
    color: var(--white);
    font-weight: 600;
    font-size: 4rem;
}

.hero-left p {
    line-height: 2rem;
    font-size: 1.5rem;
    color: var(--white);
}

.hero-left a#contactBtn {
    color: var(--white);
    background-color: var(--pri);
    border-radius: .5rem;
    padding: 1rem 2rem;
    font-size: 1.2rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    transition: 200ms;
    width: min-content;
    text-decoration: none;
    white-space: nowrap;
}

.hero-left a#contactBtn:hover {
    color: var(--pri);
    background-color: var(--dark);
    box-shadow: 0px 0px 6px -2px var(--pri);
}

.hero-left .hero-social {
    display: flex;
    justify-content: start;
    align-items: center;
    column-gap: .5rem;
    margin: 1rem 0rem;
}

.hero-left .hero-social a img {
    height: 2.5rem;
    transition: 200ms;
}

.hero-left .hero-social a:hover img {
    scale: 1.1;
}

.hero-right {
    position: relative;
    padding: 2rem 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: blue; */
}

.hero-right img#profile {
    position: relative;
    width: 100%;
    max-width: 450px;
}

@media screen and (max-width:1200px) {
    .hero-left h1 {
        font-size: 2.5rem;
    }

    .hero-left p {
        line-height: 1.2rem;
        font-size: 1rem;
    }

    .hero-left button {
        padding: .6rem 1.5rem;
        font-size: 1rem;
    }
}

@media screen and (max-width:900px) {

    .hero-left h1 {
        font-size: 1.8rem;
    }
}

@media screen and (max-width:700px) {

    .hero-left h1 {
        font-size: 2rem;
    }

    section.hero-section {
        grid-auto-flow: row;
        grid-auto-columns: 1fr;
    }

    .hero-left {
        padding: 0rem 2rem 2rem 2rem;
    }

    .hero-right {
        padding: 1rem 2rem;
        order: -1;
    }

    section.hero-section img#back_el_2 {
        opacity: .25;
    }
}