footer.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--dark);
    padding: 4rem 2rem 0rem 2rem;
}

.footer-container {
    display: grid;
    grid-template-areas:
        'left right'
        'bottom bottom';
    align-items: center;
    row-gap: 2rem;
    max-width: 1000px;
}

.footer-left {
    display: flex;
    flex-direction: column;
    grid-area: left;
}

.footer-left img {
    width: 200px;
}

.footer-left p {
    color: var(--white);
    font-size: 1.2rem;
    margin-top: 2rem;
}

.footer-left h4 {
    color: var(--white);
    margin-top: 1rem;
}

.footer-social-container {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: .5rem 0rem;
    height: 4rem;
    column-gap: 1rem;
}

.footer-social-container a {
    background-color: transparent;
    color: transparent;
    height: 100%;
    transition: 200ms;
    box-shadow: 0px 10px 30px -2px var(--pri);
}

.footer-social-container a:hover {
    scale: 1.1;
    transform: translateY(-10px);
    box-shadow: 0px 2px 30px -2px var(--pri);
}

.footer-social-container a img {
    height: 100%;
    width: auto;
}

.footer-right {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-area: right;
}

.footer-links-container {
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
    padding: 0rem 2rem;
}

.footer-links-container a {
    color: var(--white);
    text-decoration: none;
    font-size: 1.4rem;
    margin: .5rem 0rem;
    transition: 200ms;
}

.footer-links-container a:hover {
    color: var(--pri);
    scale: 1.1;
}

.footer-bottom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    grid-area: bottom;
    padding: 1rem 0rem;
}

.footer-bottom::before {
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    content: '';
    width: 100%;
    height: 3px;
    border-radius: 3rem;
    background-color: var(--pri);
    box-shadow: 0px 0px 25px 2px var(--pri);
}

.footer-bottom p {
    color: var(--white);
    margin: .1rem 0rem;
    font-size: .8rem;
    text-align: center;
}

@media screen and (max-width: 850px) {
    .footer-container {
        grid-template-areas:
            'left'
            'right'
            'bottom';
    }

    .footer-links-container {
        padding: 0rem;
    }

    .footer-links-container a {
        font-size: 1rem;
    }

}