section.what_i_do {
    display: flex;
    flex-direction: column;
    padding: 2rem 2rem;
    background-color: var(--dark);
}

section.what_i_do h1 {
    font-size: 12rem;
    font-weight: bolder;
    line-height: 8rem;
    text-align: center;
    color: var(--white);
    text-shadow: 0px 0px 30px var(--pri);
}

@media screen and (max-width: 800px) {
    section.what_i_do h1 {
        font-size: 6rem;
        line-height: 3rem;
    }
}

section.what_i_do p {
    margin-top: 2rem;
    font-size: 1.2rem;
    color: var(--white);
    margin-bottom: 3rem;
}

section.what_i_do h2 {
    font-weight: bolder;
    color: var(--white);
}

section.what_i_do ul {
    list-style-type: "📌";
    width: 100%;
}

section.what_i_do li {
    padding-left: 1rem;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    color: var(--white);
}